import { addShopInfo, getShopInfoById, getCommentByShopId } from '@/request/API/index.js';
import { mapState } from 'vuex';
export default {
	data() {
		return {
			imgURl: '',
			currentTabs: 0,
			img: '',
			shopName: '',
			phone: '',
			shopDesc: '',
			shopAddress: '',
			shopLogo: '',
			pageNo: 1,
			commentList: []
		};
	},
	onShow() {
		this.commentList=[]
		this.imgURl = imgURl;
		this.getShopInfoById();
		this.getCommentByShopId(this.pageNo, 10, this.shopId);
	},
	//上拉加载
	onReachBottom() {
		if (this.search == '') {
			this.pageNo++;
			this.getCommentByShopId(this.pageNo, 10, this.shopId);
		}
	},
	computed: {
		...mapState(['userId', 'shopId'])
	},
	methods: {
		getCommentByShopId(pageNo, pageSize, shopId) {
			getCommentByShopId(pageNo, pageSize, shopId).then(res => {
				if (res.data.code == 0) {
					if (res.data.data.list.length == 0) {
						uni.showToast({
							title: '没有更多数据了',
							icon: 'none',
							duration: 1000
						});
					}
					this.commentList = [...this.commentList, ...res.data.data.list];
				}
			});
		},
		getShopInfoById() {
			getShopInfoById(this.shopId).then(res => {
				if (res.data.code == 0) {
					(this.shopName = res.data.data.shopName),
						(this.phone = res.data.data.phone),
						(this.shopDesc = res.data.data.shopDesc),
						(this.shopAddress = res.data.data.shopAddress);
				}
			});
		},
		save() {
			if (!this.shopLogo) {
				uni.showModal({
					title: '',
					content: '请上传店铺logo图片',
					showCancel: false
				});
				return;
			}
			if (!this.shopName) {
				uni.showModal({
					title: '',
					content: '请输入店铺名称',
					showCancel: false
				});
				return;
			}

			if (!this.shopAddress) {
				uni.showModal({
					title: '',
					content: '请输入您的店铺地址',
					showCancel: false
				});
				return;
			}

			const reg = /^[1][3,4,5,7,8][0-9]{9}$/;
			if (!reg.test(this.phone)) {
				uni.showModal({
					title: '',
					content: '请输入正确的手机号码',
					showCancel: false
				});
				return;
			}
			if (!this.shopDesc) {
				uni.showModal({
					title: '',
					content: '请描述您的店铺信息',
					showCancel: false
				});
				return;
			}
			let params = {
				userId: this.userId,
				phone: this.phone,
				shopName: this.shopName,
				shopDesc: this.shopDesc,
				shopAddress: this.shopAddress,
				shopLogo: this.shopLogo
			};
			addShopInfo(params).then(res => {
				if (res.data.code == 0) {
					uni.setStorageSync('shopId', res.data.data.shop.id);
					this.$store.commit('SET_SHOPID', res.data.data.shop.id);
					uni.showToast({
						title: '保存成功'
					});
				}
			});
		},
		tabs(i) {
			this.currentTabs = i;
		},
		paizhao() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], //从相册选择
				success: res => {
					this.img = res.tempFilePaths[0];
					uni.uploadFile({
						url: baseURL + '/file/upload',
						filePath: res.tempFilePaths[0],
						name: 'file',
						formData: {
							user: 'test',
							file: res.tempFilePaths[0]
						},
						success: uploadFileRes => {
							let aaa = JSON.parse(uploadFileRes.data);
							if (aaa.code == 0) {
								console.log(uploadFileRes);
								this.shopLogo = aaa.data;
								uni.showToast({
									title: '上传成功'
								});
							}
						}
					});
				}
			});
		}
	}
};
